.currentCity {
    display: flex;
    justify-content: center;
    margin-top: 10rem;

    &__box {
        display: flex;
        min-width: 50%;
        flex-direction: column;
        align-items: center;
        border-radius: $border-radius-1;
        overflow: hidden;
        background-color: rgba($color-grey-light-2, .8);
        padding: 2rem 3rem;
        box-shadow: $box-shadow-1;
        @include respond(tab-port) {
            border-radius: 0;
            border-top: 1px solid $color-primary;
            border-bottom: 1px solid $color-primary;;
        }
        @include respond(phone) {
            padding: 5rem 0;
        }
        @include page-transition-animation(fast);
    }
}