.searchCity {
    display: flex;
    position: relative;
    justify-content: center;
    margin: 5rem 0;
    @include page-transition-animation(fast);

    &-wrapper {
        position: absolute;
        width: 40%;
        overflow: hidden;
        border-radius: $border-radius-2;
        box-shadow: $box-shadow-1;
        z-index: 10;

        @include respond(phone) {
            width: 65%;
        }
    }

    &__input, 
    &__results  {
        font-size: 2.5rem;
        padding: 1rem 2rem;
        width: 100%;
        align-self: stretch;
        background-color: $color-grey-light-2;
    }

    &__input {
        overflow: hidden;
        outline: none;
        border: none;
        color: $color-primary-dark;

        &::placeholder {
            color: $color-primary-light;
        }
    }

    &__results {
        border: none;
        padding: 0 2rem;
        background-color: rgba($color-grey-light-2, 1);
    }

    &__item {
        cursor: pointer;
        transition: all .5s;
        &:hover {
            text-indent: 3rem;
            background-color: $color-grey-dark;
            color: #fff
        }
    }
}